html, body {
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #272732;
  font-family: 'Segoe UI';
}

.home-background {
  height: 92vh;
  background: linear-gradient(58deg, rgba(2,0,36,1) 18%, rgba(16,126,148,1) 73%, rgba(41,41,59,1) 91%);
  background-size: cover;
}

.socials-background {
  height: 100vh;
  background: linear-gradient(155deg, rgba(2,0,36,1) 3%, rgba(16,126,148,1) 29%, rgba(41,41,59,1) 96%);
  background-size: cover;
}

.about-background {
  height: 100vh;
  background: linear-gradient(353deg, rgba(2,0,36,1) 13%, rgba(16,126,148,1) 24%, rgba(41,41,59,1) 86%);
  background-size: cover;
}

.nav-bar {
  margin: 0;
  width: 100vw;
  height: 8vh;
  background-color: #161618;
  white-space: nowrap;
}

.nav {
  height: 100%;
  display: inline-block;
  width: 10%;
  min-width: 90px;
  border: none;
  font-size: 100%;
  background-color: inherit;
  color: white;
  font-family: inherit;
  white-space: inherit;
}

.nav:hover {
  background-color: #59595f;
  color: #161618;
  box-shadow: 0 0 5px black;
}

.nav:active {
  background-color: white;
  color: black;
  box-shadow: 0 0 20px black;
}

#me {
  color: white;
  font-family: inherit;
  letter-spacing: .2rem;
  font-size: 11vw;
  top: 20%;
  left: 38%;
  position: absolute;
  white-space: nowrap;
  text-shadow: 0 0 13px black;
}

#more-me {
  font-family: inherit;
  font-size: 6vw;
  top: 20%;
  left: 5%;
  position: absolute;
  white-space: nowrap;
  text-shadow: 0 0 8px black;
}

#sorry {
  color: white;
  text-align: center;
  padding: 35vh 0;
}

.box-holder {
  margin-top: 8%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.social-box {
  display: inherit;
  height: 23vh;
  width: 23vh;
  margin-bottom: 6%;
  background-color: white;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 20px black;
}

.social-box a {
  color: rgb(27, 31, 133);
  text-decoration: none;
  font-family: 'Segoe UI';
  font-size: 4.5vh;
  letter-spacing: 4px;
  text-shadow: 0 0 10px black;
}

#socials {
  color: white;
  font-family: 'Segoe UI';
  font-size: 8vw;
  text-align: center;
  text-shadow: 0 0 15px black;
}

#about-me {
  color: white;
  font-size: 7vh;
  text-shadow: 0 0 10px black;
  font-family: inherit;
  text-align: center;
}

.about-box {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 80%;
  margin: auto;
}

.text {
  max-width: 50%;
}

p {
  color: white;
  text-shadow: 0 0 5px black;
  font-size: large;
  font-family: inherit;
  margin: 0 10px 20px;
  text-align: center;
}

img {
  max-width: 50%;

  max-height: 70vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 0 10px black;
  border-radius: 3px;
}

@media screen and (min-width: 1500px) {
  p {
    font-size: larger;
  }
}

@media screen and (max-width: 1160px) {
  .about-box {
    width: 100%;
  }
}

@media screen and (max-width: 930px) {
  p {
    font-size: medium;
  }

  .social-box {
    height: 17vh;
    width: 17vh;
  }
  
  .social-box a {
    font-size: 3vh;
    letter-spacing: 3px;
  }
}

@media screen and (max-width: 600px) {
  .box-holder {
    margin-top: 20%;
  }

  .about-background {
    height: 100%;
  }
}

@media screen and (max-width: 376px) {
  .social-box {
    height: 19vh;
    width: 19vh;
  }

  .social-box a {
    font-size: 4vh;
  }
}

@media screen and (max-width: 346px) {
  .nav {
    width: 7%;
    min-width: 60px;
    font-size: 70%;
  }

  #me {
    left: 36%;
  }

  #socials {
    font-size: 12vw;
  }
}

@media screen and (max-width: 296px) {
  .social-box {
    height: 16vh;
    width: 16vh;
  }

  .social-box a {
    font-size: 3vh;
    letter-spacing: 3px;
  }
}
